#githuboverview{
    background-color: #0f1624;
    padding-bottom: 100px;
}
.github_data{
    width: 70%;
    margin: auto;
    margin-top: 60px
}
.github_data>div{
    border: 1px solid #4000ff;
    border-radius: 10px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.github_data>div>img{
    height: 100%;
}
.github_data>div{
    transition: all 0.5s ease-in-out;
}
.github_data>div:hover{
    transform: scale(1.1);
}
.hours{
    margin: auto;
    margin-top: 100px;
}
.hours>div{
    border: 1px solid #4000ff;
    border-radius: 50px;
    width: 200px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #0c0032;
    transition: all 0.5s ease-in-out;
}
.hours>div:hover{
    transform: scale(1.1);
}